<template>
	<div id="statcenter-delivery-day">
		<public-header title="配送详情"></public-header>
		<div class="content padding-15 font-14 ">
			<van-row class="c-gray">
				<van-col span="5" class="padding-10-tb">配送员</van-col>
				<van-col span="3" class="padding-10-tb">总配送</van-col>
				<van-col span="7" class="padding-10-tb">普通单</br>平均配送时长（分）</van-col>
				<van-col span="6" class="padding-10-tb">超时单</br>超时率</van-col>
				<van-col span="3" class="padding-10-tb">货到付款总额</van-col>
			</van-row>
			<van-row>
				<van-col span="5">总计</van-col>
				<van-col span="3">{{stat.total_success_order}}</van-col>
				<van-col span="7">{{stat.total_normal_order + '/' + stat.avg_normal_delivery_time}}</van-col>
				<van-col span="6">{{stat.total_timeout_order + '/' + stat.percent_timeout + '%'}}</van-col>
				<van-col span="3">--</van-col>
			</van-row>
			<van-row v-for="(item, index) in detail" :key="index" v-if="detail">
				<van-col span="5">{{item.title}}</van-col>
				<van-col span="3">{{item.total_success_order}}</van-col>
				<van-col span="7">{{item.total_normal_order + '/' + item.avg_normal_delivery_time}}</van-col>
				<van-col span="6">{{item.total_timeout_order + '/' + item.percent_timeout + '%'}}</van-col>
				<van-col span="3">￥{{item.total_paytype_delivery}}</van-col>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				detail: [],
			}
		},
		methods: {
			onLoad() {
				let that = this;
				getData({
					vue: this,
					data: {
						detail: 1,
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/delivery/day',
					success: function(result) {
						that.stat = result.stat;
						that.detail = result.detail;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},

	}
</script>

<style scoped>
	#statcenter-delivery-day .van-row{
		font-size: 12px;
		text-align: center;
	}
	#statcenter-delivery-day .van-row:nth-child(odd) {
		background-color: #ddd;
	}
	#statcenter-delivery-day .van-row .van-col{
		padding: 8px 0;
		border-right: 1px solid #eee;
	}
	#statcenter-delivery-day .van-row .van-col:last-child{
		border-right: 0px;
	}
</style>


